@import url('https://fonts.googleapis.com/css2?family=Work+Sans:wght@100;200;400&display=swap');

* {
  margin: 0;
  padding: 0px;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

/* general css variables for colors and container widths */
:root {
  --color-bg: #FEF7E9;
  --color-bg-variant: #ECA831;
  --color-primary: #62465c;
  --color-primary-variant: #FFAF00;
  --color-white: #FEF7E9;
  --color-light: rgba(255, 255, 255, 0.6);
  --color-dark: #1C0A05;

  --transition: all 400ms ease;

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;
}

/* scroll/nav bar */
::-webkit-scrollbar {
  display: none;
}

body {
  font-family: "Work Sans", sans-serif;
  font-size: 20px;
  background: var(--color-bg);
  color: var(--color-dark);
  line-height: 1.7;
  align-content: center;
}

/* GENERAL STYLES FOR APP */
.container {
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1, h2, h3, h4, h5 {
  font-weight: 500;
  text-align: center;
}

h1 {
  font-size: 2.5rem;
}

section {
  margin-top: 8rem;
  height: 100%;
}

section > h2, section > h5 {
  text-align: center;
  color: var(--color-light);
}

section > h2 {
  color: var(--color-primary);
  margin-bottom: 3rem;
  text-align: center;
}

.text-light {
  color: var(--color-light);
}

a {
  color: var(--color-primary);
  transition: var(--transition);
}

a:hover {
  color: var(--color-white);
}

.btn {
  width: max-content;
  display: inline-block;
  background: var(--color-primary);
  color: var(--color-light);
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid var(--color-primary);
  transition: var(--transition);
}

.btn:hover {
  background: var(--color-primary-variant);
  color: var(--color-black);
  border-color: transparent;
}

btn-primary {
  background: var(--color-primary-variant);
  color: var(--color-bg);
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}

/* MEDIA QUERIES (MEDIUM DEVICES / TABLET) */
@media screen and (max-width: 1024px) {
  .container {
    width: var(--container-width-md)
  }

  section {
    margin-top: 6rem;
  }
}

/* MEDIA QUERIES (SMALL DEVICES / MOBILE) */
@media screen and (max-width: 600px) {
  .container {
    width: var(--container-width-sm)
  }

  section > h2 {
    margin-bottom: 2rem;
  }
}